body {
    color: $text-color;
}

html,
#root,
body,
.App {
    height: 100%;
    margin: 0;
    padding: 0;
    background-color: #303045;
}

::-webkit-scrollbar {
    width: 8px;
}

::-webkit-scrollbar-track {
    box-shadow: inset 0 0 8px grey;
}

::-webkit-scrollbar-thumb {
    background: $dark-grey;
}

::-webkit-scrollbar-thumb:hover {
    background: $grey;
}

*,
:after,
:before {
    box-sizing: border-box;
}

p{
    margin: 0;
}