@media only screen and (min-width: $sm) {
    .convention {
        &__container {
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            margin: 0 auto;
            width: 420px;
        }
    }
}

@media only screen and (min-width: 990px) {
    .convention {
        &__container {
            width: 990px;
        }
    }
}

@media only screen and (min-width: $xl) {
    .convention {
        &__container {
            width: 1200px;
        }
    }
}
