.home {
    &__image {
        position: relative;
        opacity: 0.7;
        background-attachment: fixed;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        height: 100%;
        overflow: hidden;

        &--landing-page {
            background-image: url("../images/first.jpg");
        }
        &--team {
            background-image: url("../images/gamescom-team-edited.jpg");
            height: 80%;
        }
        &--poro {
            background-image: url("../images/second.jpg");
        }
    }
    &__box {
        position: absolute;
        left: 0;
        top: 40%;
        width: 100%;
        text-align: center;
        color: #000;

        &--panel {
            background-color: rgba(200, 200, 200, 0.7);
            border-radius: 5px;
            color: black;
            padding: 45px;
            width: 60%;
            margin: 0 auto;

            @media only screen and (max-width: $md) {
                padding: 45px;
                width: 100%;
            }
        }
        &--title {
            letter-spacing: 5px;
            text-transform: uppercase;
            font: 24px "Lato";
            color: #111;
        }
        &--subtitle {
            letter-spacing: 5px;
            text-transform: uppercase;
            font: 16px "Lato", sans-serif;
            color: #111;
        }
        &--full-height {
            height: 100%;
            padding-top: 20%;
        }
    }
    &__container {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        margin: 0 auto;
        padding: 30px 50px;
        text-align: center;
        z-index: 1;

        &--tall {
            height: 480px;
        }

        &--light {
            background-color: white;
        }

        &--dark {
            background-color: #222;
            color: white;
        }
        &--margin {
            margin: 0 auto;
            width: 50%;
        }
    }
}

@media only screen and (max-width: $md) {
    .home {
        &__container {
            padding: 30px 60px;
        }
    }
}